import React from 'react';
import { getValidMValues, getQuantumConstraints, calculateProperties } from '../utils/sphericalHarmonics';

interface ControlPanelProps {
  l: number;
  m: number;
  onLChange: (l: number) => void;
  onMChange: (m: number) => void;
}

const ControlPanel: React.FC<ControlPanelProps> = ({ l, m, onLChange, onMChange }) => {
  const validMValues = getValidMValues(l);
  const constraints = getQuantumConstraints(l, m);
  const properties = calculateProperties(l, m);

  return (
    <div className="card">
      <h3 className="text-lg font-semibold mb-4">Quantum Numbers</h3>
      
      <div className="grid gap-4">
        {/* l (orbital angular momentum) control */}
        <div>
          <label className="label">Orbital Angular Momentum (l)</label>
          <div className="flex gap-2">
            <input
              type="number"
              min="0"
              max="8"
              value={l}
              onChange={(e) => onLChange(parseInt(e.target.value) || 0)}
              className="input flex-1"
            />
            <button
              onClick={() => onLChange(Math.max(0, l - 1))}
              className="btn btn-secondary"
              disabled={l <= 0}
            >
              -
            </button>
            <button
              onClick={() => onLChange(Math.min(8, l + 1))}
              className="btn btn-secondary"
              disabled={l >= 8}
            >
              +
            </button>
          </div>
          <p className="text-sm text-secondary mt-1">
            l = {l} (total angular momentum quantum number)
          </p>
        </div>

        {/* m (magnetic quantum number) control */}
        <div>
          <label className="label">Magnetic Quantum Number (m)</label>
          <div className="flex gap-2">
            <input
              type="number"
              min={-l}
              max={l}
              value={m}
              onChange={(e) => onMChange(parseInt(e.target.value) || 0)}
              className="input flex-1"
            />
            <button
              onClick={() => onMChange(Math.max(-l, m - 1))}
              className="btn btn-secondary"
              disabled={m <= -l}
            >
              -
            </button>
            <button
              onClick={() => onMChange(Math.min(l, m + 1))}
              className="btn btn-secondary"
              disabled={m >= l}
            >
              +
            </button>
          </div>
          <p className="text-sm text-secondary mt-1">
            m = {m} (z-component of angular momentum)
          </p>
        </div>

        {/* Quick m selection */}
        <div>
          <label className="label">Quick m Selection</label>
          <div className="flex flex-wrap gap-2">
            {validMValues.map((mValue) => (
              <button
                key={mValue}
                onClick={() => onMChange(mValue)}
                className={`btn ${m === mValue ? '' : 'btn-secondary'}`}
                style={{ minWidth: '40px' }}
              >
                {mValue}
              </button>
            ))}
          </div>
        </div>

        {/* Validation status */}
        <div className={`p-3 rounded-lg ${constraints.valid ? 'bg-green-50 border border-green-200' : 'bg-red-50 border border-red-200'}`}>
          <p className={`text-sm font-medium ${constraints.valid ? 'text-green-800' : 'text-red-800'}`}>
            {constraints.message}
          </p>
        </div>
      </div>

      {/* Properties display */}
      <div className="mt-6">
        <h4 className="font-semibold mb-3">Quantum Properties</h4>
        <div className="grid grid-2 gap-4">
          <div className="p-3 bg-blue-50 rounded-lg">
            <p className="text-sm font-medium text-blue-800">L Eigenvalue</p>
            <p className="text-lg font-semibold text-blue-900">{properties.L2}</p>
          </div>
          <div className="p-3 bg-green-50 rounded-lg">
            <p className="text-sm font-medium text-green-800">L_z Eigenvalue</p>
            <p className="text-lg font-semibold text-green-900">{properties.Lz}</p>
          </div>
          <div className="p-3 bg-purple-50 rounded-lg">
            <p className="text-sm font-medium text-purple-800">Total Angular Momentum</p>
            <p className="text-lg font-semibold text-purple-900">{properties.L2}  {properties.totalAngularMomentum.toFixed(3)}</p>
          </div>
          <div className="p-3 bg-orange-50 rounded-lg">
            <p className="text-sm font-medium text-orange-800">Degeneracy</p>
            <p className="text-lg font-semibold text-orange-900">{properties.degeneracy} states</p>
          </div>
        </div>
      </div>

      {/* Educational info */}
      <div className="mt-6 p-4 bg-gray-50 rounded-lg">
        <h4 className="font-semibold mb-2">Physical Interpretation</h4>
        <ul className="text-sm text-gray-700 space-y-1">
          <li> <strong>l</strong> determines the total angular momentum: L = (l(l+1))</li>
          <li> <strong>m</strong> determines the z-component: L_z = m</li>
          <li> For each l, there are 2l+1 possible m values: -l, -l+1, ..., l-1, l</li>
          <li> The spherical harmonic Y_l^m describes the angular part of the wavefunction</li>
        </ul>
      </div>
    </div>
  );
};

export default ControlPanel;
