import React, { useState } from 'react';
import { sphericalHarmonic } from '../utils/sphericalHarmonics';

interface ComputationPanelProps {
  l: number;
  m: number;
}

const ComputationPanel: React.FC<ComputationPanelProps> = ({ l, m }) => {
  const [theta, setTheta] = useState(45);
  const [phi, setPhi] = useState(90);
  const [useRadians, setUseRadians] = useState(false);

  const thetaRad = useRadians ? theta : (theta * Math.PI) / 180;
  const phiRad = useRadians ? phi : (phi * Math.PI) / 180;

  const result = sphericalHarmonic(l, m, thetaRad, phiRad);

  const formatComplex = (real: number, imag: number) => {
    if (Math.abs(imag) < 1e-10) {
      return real.toFixed(6);
    }
    if (Math.abs(real) < 1e-10) {
      return `${imag >= 0 ? '' : '-'}${Math.abs(imag).toFixed(6)}i`;
    }
    return `${real.toFixed(6)} ${imag >= 0 ? '+' : '-'} ${Math.abs(imag).toFixed(6)}i`;
  };

  return (
    <div className="card">
      <h3 className="text-lg font-semibold mb-4">Compute Values</h3>
      
      <div className="grid gap-4">
        {/* Angle inputs */}
        <div className="grid grid-2 gap-4">
          <div>
            <label className="label"> (theta) - Polar Angle</label>
            <input
              type="number"
              min="0"
              max={useRadians ? "6.28" : "360"}
              step={useRadians ? "0.1" : "1"}
              value={theta}
              onChange={(e) => setTheta(parseFloat(e.target.value) || 0)}
              className="input"
            />
            <p className="text-sm text-secondary mt-1">
              {useRadians ? `${theta.toFixed(2)} rad` : `${theta}`} 
              {!useRadians && ` (${(theta * Math.PI / 180).toFixed(3)} rad)`}
            </p>
          </div>
          
          <div>
            <label className="label"> (phi) - Azimuthal Angle</label>
            <input
              type="number"
              min="0"
              max={useRadians ? "6.28" : "360"}
              step={useRadians ? "0.1" : "1"}
              value={phi}
              onChange={(e) => setPhi(parseFloat(e.target.value) || 0)}
              className="input"
            />
            <p className="text-sm text-secondary mt-1">
              {useRadians ? `${phi.toFixed(2)} rad` : `${phi}`}
              {!useRadians && ` (${(phi * Math.PI / 180).toFixed(3)} rad)`}
            </p>
          </div>
        </div>

        {/* Units toggle */}
        <div className="flex items-center gap-2">
          <input
            type="checkbox"
            id="useRadians"
            checked={useRadians}
            onChange={(e) => setUseRadians(e.target.checked)}
            className="rounded"
          />
          <label htmlFor="useRadians" className="text-sm font-medium">
            Use radians instead of degrees
          </label>
        </div>

        {/* Quick angle presets */}
        <div>
          <label className="label">Quick Angle Presets</label>
          <div className="flex flex-wrap gap-2">
            {[
              { name: 'North Pole', theta: 0, phi: 0 },
              { name: 'South Pole', theta: 180, phi: 0 },
              { name: 'Equator X', theta: 90, phi: 0 },
              { name: 'Equator Y', theta: 90, phi: 90 },
              { name: '45 XY', theta: 45, phi: 45 },
            ].map((preset) => (
              <button
                key={preset.name}
                onClick={() => {
                  setTheta(useRadians ? (preset.theta * Math.PI) / 180 : preset.theta);
                  setPhi(useRadians ? (preset.phi * Math.PI) / 180 : preset.phi);
                }}
                className="btn btn-secondary text-xs"
              >
                {preset.name}
              </button>
            ))}
          </div>
        </div>

        {/* Results display */}
        <div className="mt-6 p-4 bg-blue-50 rounded-lg">
          <h4 className="font-semibold mb-3 text-blue-900">Y<sub>{l}</sub><sup>{m}</sup>(, ) =</h4>
          
          <div className="grid gap-3">
            <div className="p-3 bg-white rounded border">
              <p className="text-sm font-medium text-gray-600">Complex Value</p>
              <p className="text-lg font-mono text-blue-900">
                {formatComplex(result.real, result.imag)}
              </p>
            </div>
            
            <div className="grid grid-2 gap-3">
              <div className="p-3 bg-white rounded border">
                <p className="text-sm font-medium text-gray-600">Real Part</p>
                <p className="text-lg font-mono text-green-900">
                  {result.real.toFixed(6)}
                </p>
              </div>
              
              <div className="p-3 bg-white rounded border">
                <p className="text-sm font-medium text-gray-600">Imaginary Part</p>
                <p className="text-lg font-mono text-purple-900">
                  {result.imag.toFixed(6)}
                </p>
              </div>
            </div>
            
            <div className="p-3 bg-white rounded border">
              <p className="text-sm font-medium text-gray-600">Magnitude</p>
              <p className="text-lg font-mono text-orange-900">
                {result.magnitude.toFixed(6)}
              </p>
            </div>
          </div>
        </div>

        {/* Mathematical formula */}
        <div className="mt-4 p-4 bg-gray-50 rounded-lg">
          <h4 className="font-semibold mb-2">Mathematical Formula</h4>
          <div className="text-sm text-gray-700 space-y-2">
            <p>Y<sub>l</sub><sup>m</sup>(, ) = [(2l+1)/(4)  (l-|m|)!/(l+|m|)!]  P<sub>l</sub><sup>|m|</sup>(cos )  e<sup>im</sup></p>
            <p>Where:</p>
            <ul className="ml-4 space-y-1">
              <li> P<sub>l</sub><sup>m</sup> is the associated Legendre polynomial</li>
              <li>  is the polar angle (0 to )</li>
              <li>  is the azimuthal angle (0 to 2)</li>
              <li> l is the orbital angular momentum quantum number</li>
              <li> m is the magnetic quantum number</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ComputationPanel;
